<template>
  <nav class="panel">
    <a
      class="panel-block"
      v-for="(memo, index) in memos"
      :key="index"
      @click="showEdit(memo, index)"
    >
      <div class="memos">{{ summary(memo) }}</div>
    </a>
  </nav>
  <button class="button is-floating is-success" @click="showEdit(null)">
    <i class="fas fa-plus"></i>
  </button>
</template>

<script lang="ts">
import { Vue, Options } from "vue-class-component";
import Memo from "@/models/memo";

@Options({
  props: {
    memos: Array,
  },
})
export default class List extends Vue {
  summary(memo: string): string {
    return memo.split("\n")[0];
  }

  showEdit(memo: string | null, index: number | null): void {
    this.$emit(
      "show-edit",
      memo ? new Memo({ id: index, memo: memo }) : new Memo()
    );
  }
}
</script>

<style lang="scss">
.memos {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
</style>
