<template>
  <div v-if="memo">
    <div class="field">
      <div class="control">
        <textarea
          class="textarea"
          placeholder="Memo"
          v-model="memo.memo"
        ></textarea>
      </div>
    </div>
    <div class="field is-grouped">
      <div class="control">
        <button class="button is-primary" @click="apply" :disabled="!valid">
          Save
        </button>
      </div>
      <div class="control">
        <button class="button is-danger" @click="remove" v-if="!isCreate">
          Delete
        </button>
      </div>
      <div class="control">
        <button class="button is-light" @click="cancel">Cancel</button>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Vue, Options } from "vue-class-component";
import { Prop } from "vue-property-decorator";
import Swal from "sweetalert2";
import { SweetAlertResult } from "sweetalert2";
import Memo from "@/models/memo";

@Options({})
export default class Editor extends Vue {
  @Prop({ type: Memo })
  memo: Memo = new Memo();

  get valid(): boolean {
    return this.memo && 0 < this.memo.memo.length;
  }

  get isCreate(): boolean {
    return !this.memo || this.memo.id === null;
  }

  apply(): void {
    Swal.fire({
      text: "Do you want to save?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Save",
      showLoaderOnConfirm: true,
    }).then((response: SweetAlertResult) => {
      if (response.isConfirmed) {
        this.$emit("apply", this.memo);
      }
    });
  }

  remove(): void {
    Swal.fire({
      text: "Do you want to delete?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Delete",
      showLoaderOnConfirm: true,
    }).then((response: SweetAlertResult) => {
      if (response.isConfirmed) {
        this.$emit("remove", this.memo);
      }
    });
  }

  cancel(): void {
    this.$emit("cancel");
  }
}
</script>

<style lang="scss"></style>
