<template>
  <div class="app">
    <header>
      <nav
        class="navbar is-primary"
        role="navigation"
        aria-label="main navigation">
        <div class="navbar-brand">
          <a class="navbar-item" href="#">Memo</a>
        </div>
      </nav>
    </header>
    <main>
      <section class="section">
        <div class="cotainer">
          <List :memos="memos" @show-edit="showEdit" v-if="list" />
          <Editor
            :memo="memo"
            @apply="apply"
            @remove="remove"
            @cancel="cancel"
            v-if="editor"
          />
        </div>
      </section>
    </main>
    <footer class="footer">
      <div class="content has-text-centered">
        <p><strong>Memo</strong> by Yuuki Sumida.</p>
      </div>
    </footer>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import Editor from "@/components/Editor.vue";
import List from "@/components/List.vue";
import Memo from "@/models/memo";

@Options({
  components: {
    Editor,
    List,
  },
})
export default class App extends Vue {
  memos: Array<string> = [];
  memo: Memo | null = null;

  mounted(): void {
    const memosString = localStorage.getItem("memos");
    if (memosString) {
      this.memos = JSON.parse(memosString) as Array<string>;
    }
  }

  get list(): boolean {
    return this.memo === null;
  }

  get editor(): boolean {
    return !this.list;
  }

  apply(memo: Memo): void {
    if (memo.id === null) {
      this.memos.unshift(memo.memo);
    } else {
      this.memos.splice(memo.id, 1, memo.memo);
    }
    this.memo = null;
    localStorage.setItem("memos", JSON.stringify(this.memos));
  }

  remove(memo: Memo): void {
    if (memo.id !== null) {
      this.memos.splice(memo.id, 1);
    }
    this.memo = null;
    localStorage.setItem("memos", JSON.stringify(this.memos));
  }

  cancel(): void {
    this.memo = null;
  }

  showEdit(memo: Memo | null): void {
    this.memo = memo ? memo : new Memo();
  }
}
</script>

